<template>
    <v-main>
      <div>
        <v-row class="my-8" align="center" justify="center">
          <v-col cols="7" align="center">
            <h1 style="margin: 15px 0;">ESTADO DE PASAPORTE</h1>
            <ValidationObserver ref="observer">
              <form @submit.prevent="searchPassport">
                <v-card outlined style="border-radius: 15px;">
                    <v-card-text>
                      <div class="d-flex flex-row justify-space-around">
                        <v-col cols="12" sm="6">
                          <ValidationProvider v-slot="{ errors }" name="field2" rules="required">
                            <v-text-field
                              id="name"
                              v-model="name"
                              :hide-details="errors == '' ? true : false"
                              outlined
                              rounded
                              label="PRIMER NOMBRE"
                              type="text"
                              :error-messages="errors"
                            ></v-text-field>
                          </ValidationProvider>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <ValidationProvider v-slot="{ errors }" name="birthDate" rules="required">
                              <v-menu
                                id="birthDate"
                                :close-on-content-click="false"
                                transition="scale-transition"
                                offset-y
                                max-width="290px"
                                min-width="auto"
                              >
                                <template v-slot:activator="{ on, attrs }">
                                  <v-text-field
                                    v-model="computedDateFormatted"
                                    label="FECHA DE NACIMIENTO"
                                    append-icon="mdi-calendar"
                                    readonly
                                    :hide-details="errors == '' ? true : false"
                                    clearable
                                    outlined
                                    rounded
                                    v-bind="attrs"
                                    v-on="on"
                                    :error-messages="errors"
                                    @click:clear="birthDate = null"
                                  ></v-text-field>
                                </template>
                                <v-date-picker
                                  v-model="birthDate"
                                  no-title
                                  scrollable
                                ></v-date-picker>
                            </v-menu>
                          </ValidationProvider>
                        </v-col>
                      </div>
                    </v-card-text>
              </v-card>
              <div class="pt-10 d-flex justify-center">
                  <v-btn plain rounded elevation="2" type="submit">BUCAR</v-btn>
              </div>
              </form>
            </ValidationObserver>
          </v-col>
        </v-row>
      </div>

      <v-divider/>

      <MostUsedServices/>

      <v-divider/>

      <BestSeller/>

      <BestNotices/>
    </v-main>
</template>

<script>
import MostUsedServices from '@/components/MostUsedServices'
import BestSeller from '@/components/BestSeller'
import BestNotices from '@/components/BestNotices'
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('required', {
  validate (value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    }
  },
  message: 'Rellene este campo.',
  computesRequired: true
})

export default {
  name: 'PassportTracking',
  components: {
    MostUsedServices,
    BestSeller,
    BestNotices,
    ValidationObserver,
    ValidationProvider
  },
  data: vm => ({
    name: null,
    birthDate: null,
    date: new Date().toISOString().substr(0, 10),
    dateFormatted: vm.formatDate(new Date().toISOString().substr(0, 10)),
    menu1: false
  }),

  computed: {
    computedDateFormatted () {
      return this.formatDate(this.birthDate)
    }
  },

  watch: {
    date (val) {
      this.dateFormatted = this.formatDate(this.date)
    }
  },

  methods: {
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${month}/${day}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [month, day, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    searchPassport () {
      this.$refs.observer.validate()
    }
  }
}
</script>

