<template>
  <v-main :class="this.$route.name == 'blogs-section' ? 'blog-section-background-image' : 'blogs-background-image' ">

    <Navigation />

    <div id="most-reader-section" v-if="this.$route.name == 'blogs-section'">
      <div id="blog-detail-container"></div>

      <v-row style="margin: 6rem 4rem 11rem 4rem;">
            <v-card elevation="0" color="transparent" min-width="100%" max-width="100%">
              <v-row class="pt-6">
                <v-col align="center" cols="6">
                  <v-img
                    class="blog-box-shadow rounded-xl"
                    style="border-radius: 16px;"
                    min-width="500px" max-width="500px" min-height="400px"
                    :src="getImgUrl(mostRecentBlog.image)"
                    :lazy-src="getImgUrl(mostRecentBlog.image)"
                  ></v-img>
                </v-col>
                <v-col cols="5" class="pt-8">
                  <span id="title">{{ mostRecentBlog.title }}</span>
                  <p>{{ mostRecentBlog.text }}</p>
                  <v-row justify="end" class="bottons-row">
                      <v-btn id="read-btn" @click="goToDetails(mostRecentBlog)">LEER MÁS</v-btn>
                  </v-row>
                </v-col>
              </v-row>
            </v-card>
      </v-row>

      <BlogsList section="recents" :listSize="9"/>

      <v-row style="margin: 6rem 4rem 12rem;">
        <v-card elevation="0" color="transparent" min-width="100%" max-width="100%">
          <v-row class="pt-6">
            <v-col align="center" cols="6">
              <v-img
                class="blog-box-shadow rounded-xl"
                style="border-radius: 16px;"
                min-width="500px" max-width="500px" min-height="400px"
                :src="getImgUrl(mostReadedBlog.image)"
                :lazy-src="getImgUrl(mostReadedBlog.image)"
              ></v-img>
            </v-col>
            <v-col cols="5" class="pt-8">
              <span id="title">{{ mostReadedBlog.title }}</span>
              <p>{{ mostReadedBlog.text }}</p>
              <v-row justify="end" class="bottons-row">
                  <v-btn id="read-btn" @click="goToDetails(mostReadedBlog)">LEER MÁS</v-btn>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-row>

      <BlogsList section="most-readed" title="Artículos más destacados" :listSize="9"/>
    </div>

    <div id="sections" v-if="this.$route.name == 'recent-blogs-section' || this.$route.name == 'most-readed-blogs-section'">
      <MenuStore/>
      <BlogsList />
    </div>

  </v-main>
</template>

<script>
import Navigation from '@/components/Navigation'
import MenuStore from '@/components/MenuStore'
import BlogsList from '@/components/BlogsList'
import router from '../../router'

export default {
  name: 'Blogs',
  components: {
    Navigation,
    BlogsList,
    MenuStore
  },
  data () {
    return {
      showAll: false,
      mostRecentBlog: this.$store.getters.getBlog(0),
      mostReadedBlog: this.$store.getters.getBlog(0)
    }
  },
  methods: {
    getImgUrl (image) {
      var images = require.context('../../assets/', false)
      return images('./' + image)
    },
    showMore (section) {
      this.showAll = true
      router.push({ path: this.route.path + '-' + section.toLowerCase() })
    },
    goToDetails (blog) {
      router.push({ path: '/blogs/' + blog.section.toLowerCase() + '/' + blog.id })
    }
  }
}
</script>

