<template>
  <div>
        <section v-if="slideMode" id="products-container">
          <v-row justify="space-between" align="center">
            <v-col id="section-title" cols="3">
              <span>TIENDA CUBAMAX</span>
            </v-col>
            <v-col id="all-products-button" class="d-flex flex-column align-center" cols="4">
              <v-btn elevation="0">VER TODAS LOS PRODUCTOS</v-btn>
            </v-col>
          </v-row>
          <div class="product-slider-container">
            <v-slide-group
              id="products-slider"
              class="pa-2 d-flex flex-row align-center justify-center"
              center-active
              active-class="success"
              show-arrows="always"
            >
              <v-slide-item v-for="product in products" :key="product.name" v-slot="{ toggle }">
                <v-hover>
                  <template v-slot:default="{ hover }">

                    <Product :product="product" :hover="hover" @click="toggle"/>

                  </template>
                </v-hover>
              </v-slide-item>
            </v-slide-group>
          </div>
        </section>

        <v-row v-if="!slideMode && !listSize" class="px-15" dense>
          <Product v-for="(item, i) in pageProducts" :key="i" :hover="hover" :product="item" />
        </v-row>
        <v-row v-if="!slideMode && listSize" class="px-15" dense>
          <Product v-for="(item, i) in products.slice(0, listSize)" :key="i" :product="item" />
        </v-row>
        <div v-if="!slideMode && !listSize" class="text-center">
            <v-pagination circle
            v-model="pagination.page"
            :length="Math.ceil(this.products.length / 12)"
            :total-visible="15"
            @input="paginate"
            @next="paginate"
            @previous="paginate"
            ></v-pagination>
        </div>
        <v-row v-if="!slideMode && listSize">
          <v-col id="all-products-button" class="d-flex flex-column align-center">
            <v-btn elevation="0" style="margin-left: 0 !important; margin: 20px 0px 12px 0px !important;">Cargar más productos</v-btn>
          </v-col>
        </v-row>
  </div>
</template>

<script>
import Product from '@/components/Product'

export default {
  name: 'ProductsList',
  props: {
    slideMode: {
      type: Boolean,
      default: false
    },
    listSize: {
      type: Number,
      required: false
    }
  },
  components: {
    Product
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      products: this.$store.getters.products(),
      pageProducts: []
    }
  },
  created: function () {
    this.pageProducts = this.products.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
  },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    getImgUrl (image) {
      var images = require.context('../assets', false, /\.jpeg$/)
      return images('./' + image + '.jpeg')
    },
    seeDetails (product) {
      this.$store.dispatch('updateProductDetails', product)
    },
    paginate () {
      this.pagination.offset = this.pagination.limit * this.pagination.page - this.pagination.limit
      this.pageProducts = this.products.slice(this.pagination.offset, this.pagination.limit * this.pagination.page)
    }
  }
}
</script>

