<template>
  <v-main>
    <div>
      <v-row class="my-8" align="center" justify="center" outlined>
        <v-col cols="7" align="start">
          <v-card
            class="d-flex flex-column justify-space-around"
            style="border-radius: 15px"
          >
            <ValidationObserver ref="observer">
                <v-form @submit.prevent="sendForm">

                  <v-card-text>
                    <div class="d-flex flex-row justify-space-around">
                      <v-col cols="6">
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="importe"
                          rules="required|moreThanZero"
                        >
                          <v-text-field
                            dense
                            id="importe"
                            v-model="form.importe"
                            :error-messages="errors"
                            :hide-details="errors == '' ? true : false"
                            outlined
                            rounded
                            type="number"
                            min="1"
                            label="Importe a recibir en Cuba"
                            append-icon="mdi-currency-usd"
                          ></v-text-field>
                        </ValidationProvider>
                      </v-col>
                      <v-col cols="6">
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="deliveryCurrency"
                          rules="required"
                        >
                          <v-select
                            dense
                            id="deliveryCurrency"
                            v-model="form.deliveryCurrency"
                            :error-messages="errors"
                            :hide-details="errors == '' ? true : false"
                            :items="deliveryCurrencyList"
                            item-text="name"
                            item-value="name"
                            label="Moneda de entrega"
                            outlined
                            rounded
                          ></v-select>
                        </ValidationProvider>
                      </v-col>
                    </div>

                    <div class="d-flex flex-row justify-space-around">
                      <v-col cols="6">
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="deliveryWay"
                          rules="required"
                        >
                          <v-select
                            dense
                            id="deliveryWay"
                            v-model="form.deliveryWay"
                            :error-messages="errors"
                            :hide-details="errors == '' ? true : false"
                            :items="deliveryWayList"
                            item-text="name"
                            label="Forma de entrega"
                            outlined
                            rounded
                            return-object
                            single-line
                          ></v-select>
                        </ValidationProvider>
                      </v-col>
                      <v-col cols="6">
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="totalToPay"
                          rules="required|moreThanZero"
                        >
                          <v-text-field
                            dense
                            id="totalToPay"
                            v-model="form.totalToPay"
                            :error-messages="errors"
                            :hide-details="errors == '' ? true : false"
                            rounded
                            outlined
                            type="number"
                            min="1"
                            label="Total a pagar a CUBAMAX"
                            append-icon="mdi-currency-usd"
                          ></v-text-field>
                        </ValidationProvider>
                      </v-col>
                    </div>

                    <v-col cols="12">
                      <v-simple-table
                        dense
                        outlined
                        style="
                          width: 70% !important;
                          margin-top: 1rem;
                          border: 1px solid lightgray;
                        "
                      >
                        <template v-slot:default>
                          <thead>
                            <tr>
                              <th class="text-right">CUBA</th>
                              <th class="text-left"></th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td>USTED ENVÍA</td>
                              <td class="text-right">{{ form.importe || "0.00" }} USD</td>
                            </tr>
                            <tr>
                              <td>FEE</td>
                              <td class="text-right">0.00 USD</td>
                            </tr>
                            <tr>
                              <td>TOTAL A PAGAR</td>
                              <td class="text-right">
                                {{ form.totalToPay || "0.00" }} USD
                              </td>
                            </tr>
                            <tr>
                              <td>DESTINARIO RECIBE</td>
                              <td class="text-right">{{ form.deliveryCurrency ? (form.importe || "0.00") : '0.00' }} {{form.deliveryCurrency}}</td>
                            </tr>
                            <tr>
                              <td>TASA DE CAMBIO</td>
                              <td class="text-right"></td>
                            </tr>
                          </tbody>
                        </template>
                      </v-simple-table>
                    </v-col>
                  </v-card-text>

                  <v-card-actions class="pb-4 d-flex flex-row justify-center">
                    <v-btn plain rounded elevation="2" type="submit">ENVIAR</v-btn>
                  </v-card-actions>

              </v-form>
            </ValidationObserver>
          </v-card>
        </v-col>
      </v-row>
    </div>

    <v-divider />

    <Message />

    <v-divider />

    <Content />

    <v-divider />

    <v-divider></v-divider>

    <MostUsedServices />

    <v-divider></v-divider>

    <BestSeller />

    <BestNotices />
  </v-main>
</template>

<script>
import Message from '@/components/Message'
import Content from '@/components/Content'
import MostUsedServices from '@/components/MostUsedServices'
import BestSeller from '@/components/BestSeller'
import BestNotices from '@/components/BestNotices'
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required } from 'vee-validate/dist/rules'

extend('required', {
  ...required,
  message: 'Este campo es requerido.'
})

extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor válido.'
})

export default {
  name: 'Remittance',
  components: {
    Message,
    Content,
    MostUsedServices,
    BestSeller,
    BestNotices,
    ValidationObserver,
    ValidationProvider
  },
  data: () => ({
    form: {
      importe: null,
      deliveryCurrency: null,
      deliveryWay: null,
      totalToPay: null
    },
    deliveryWayList: [{ name: 'Tarjeta' }, { name: 'Cash' }],
    deliveryCurrencyList: [{ name: 'USD' }, { name: 'EU' }, { name: 'MN' }]
  }),
  methods: {
    sendForm () {
      this.$refs.observer.validate()
    }
  }
}
</script>

<style scoped>
.recharge-card-title {
  font-size: small;
  padding-top: 0;
  padding-bottom: 0;
}
.recharge-card-subtitle {
  font-size: small;
  padding: 12px 16px;
  text-align: justify;
}
</style>
