<template>
  <v-main>

    <v-img
      src="../../assets/cubamax.jpeg">
      <v-row
        class="fill-height ma-0"
        align="center"
        justify="center">
          <v-stepper v-model="e1" class="">
            <h1 class="text-center">Recarga Nauta</h1>
            <v-stepper-header>
              <v-divider></v-divider>
              <v-stepper-step
                :complete="e1 > 1"
                step="1"
                color="warning"
              >
                Paso 1
              </v-stepper-step>

              <v-divider></v-divider>

              <v-stepper-step
                :complete="e1 > 2"
                step="2"
                color="warning"
              >
                Paso 2
              </v-stepper-step>

              <v-divider></v-divider>
            </v-stepper-header>

            <v-stepper-items>
              <v-stepper-content step="1">
                <v-card style='border-radius: 15px'>
                  <v-row style='padding: 15px'>

                      <v-sheet>
                        <v-slide-group
                          v-model="itemSelect"
                          class="pa-4">
                            <v-slide-item
                              v-slot="{ active, toggle }"
                              v-for="(offer, i) in 3"
                              :key="i">
                                <v-col
                                  cols='12'
                                  sm='6'
                                  md='4'
                                  style='padding-top: 20px padding-bottom: 20px'>
                                    <v-card
                                      elevation='2'
                                      width="200px"
                                      @click="toggle"
                                      :color="active ? 'warning' : 'grey lighten-1'">
                                        <v-card-title class='recharge-card-title'>
                                            OFERTA {{ i + 1 }}
                                        </v-card-title>
                                        <v-card-subtitle class='recharge-card-subtitle'>
                                          Descripcion de la oferta</v-card-subtitle>
                                      </v-card>
                                </v-col>
                            </v-slide-item>
                        </v-slide-group>
                      </v-sheet>
                  </v-row>
                  <v-card-actions class='pb-4 d-flex justify-center'>
                    <v-btn
                      elevation='2'
                      @click='itemSelect !== null? e1 = 2: e1 = 1'
                      color="warning">CONTINUAR</v-btn
                    >
                  </v-card-actions>
                </v-card>
              </v-stepper-content>

              <v-stepper-content step="2">
                <v-card style='border-radius: 15px'  height="150px">
                  <v-row style='padding: 15px'> </v-row>
                  <v-card-actions class='pb-4 d-flex justify-center' >
                    <v-btn
                      elevation='2'
                      @click='recharge'
                      color="warning">RECARGAR</v-btn
                    >
                    <v-btn
                      elevation='2'
                      @click='e1 = 1'
                      color="warning">Atrás</v-btn
                    >
                  </v-card-actions>
                </v-card>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
      </v-row>
    </v-img>

    <v-divider />

    <Content />

    <v-divider />

    <MostUsedServices />

    <v-divider />

    <BestSeller />

    <BestNotices />
  </v-main>
</template>

<script>
import Content from '@/components/Content'
import MostUsedServices from '@/components/MostUsedServices'
import BestSeller from '@/components/BestSeller'
import BestNotices from '@/components/BestNotices'

export default {
  name: 'RechargeNauta',
  components: {
    Content,
    MostUsedServices,
    BestSeller,
    BestNotices
  },
  data: () => {
    return {
      itemSelect: null,
      e1: 1
    }
  },
  methods: {
    recharge () {
      alert('Recargando la tarjeta...')
    }
  }
}
</script>

<style scoped>
.recharge-card-title {
  font-size: small;
  padding-top: 0;
  padding-bottom: 0;
}
.recharge-card-subtitle {
  font-size: small;
  padding: 12px 16px;
  text-align: justify;
}
</style>
