<template>
    <v-main>
            <v-img :src="getImgUrl('cubamax')">

                <v-row
                  class="my-8"
                  align="center"
                  justify="center">
                    <v-col cols="7" align="center">
                      <v-form @submit.prevent="makeRequest">
                        <h1 style="margin: 15px 0; color: white">LLAMADAS A CUBA</h1>
                        <v-card style="border-radius: 15px;">
                          <ValidationObserver ref="observer">
                              <v-row class="ma-4" style="padding: 30px 0;">
                                  <v-col cols="6">
                                    <ValidationProvider
                                      v-slot="{ errors }"
                                      name="amountRecharge"
                                      rules="required|moreThanZero">
                                        <v-text-field dense
                                              id="amountRecharge"
                                              v-model="form.amountRecharge"
                                              :hide-details="errors == '' ? true : false"
                                              outlined
                                              rounded
                                              label="CANTIDAD A RECARGAR"
                                              append-icon="mdi-currency-usd"
                                              type="number"
                                              min="1"
                                              :error-messages="errors"
                                            ></v-text-field>
                                    </ValidationProvider>
                                  </v-col>

                                  <v-col
                                    cols="6">
                                    <ValidationProvider
                                      v-slot="{ errors }"
                                      name="accountBalance"
                                      rules="required|moreThanZero">
                                            <v-text-field dense
                                              id="accountBalance"
                                              v-model="form.accountBalance"
                                              :hide-details="errors == '' ? true : false"
                                              outlined
                                              rounded
                                              type="number"
                                              append-icon="mdi-currency-usd"
                                              label="BALANCE A SU CUENTA"
                                              :error-messages="errors"
                                            ></v-text-field>
                                    </ValidationProvider>
                                  </v-col>
                              </v-row>

                          </ValidationObserver>
                        </v-card>
                        <v-card-actions class="justify-center ma-2">
                            <v-btn rounded raised elevation="2" type="submit">Solicitar</v-btn>
                        </v-card-actions>
                      </v-form>
                    </v-col>
                </v-row>
            </v-img>

        <v-divider/>

        <Content/>

        <v-divider/>

        <MostUsedServices/>
        <v-divider></v-divider>

        <BestSeller/>

        <BestNotices/>

        <v-divider></v-divider>
  </v-main>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required } from 'vee-validate/dist/rules'
import Content from '@/components/Content'
import MostUsedServices from '@/components/MostUsedServices'
import BestSeller from '@/components/BestSeller'
import BestNotices from '@/components/BestNotices'

extend('required', {
  ...required,
  message: 'Este campo es requerido.'
})

extend('moreThanZero', {
  validate (value) {
    return value >= 1 && !value.toString().includes('-')
  },
  message: 'Escoja un valor válido.'
})

export default {
  name: 'CallToCuba',
  components: {
    Content,
    MostUsedServices,
    BestSeller,
    BestNotices,
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      form: {
        amountRecharge: '',
        accountBalance: ''
      }
    }
  },
  methods: {
    getImgUrl (image) {
      var images = require.context('../../assets/', false, /\.jpeg$/)
      return images('./' + image + '.jpeg')
    },
    makeRequest () {
      this.$refs.observer.validate()
        .then(valid => {
          if (valid) {
            alert('Solicitud enviada')
          }
        })
    }
  }
}
</script>

<style scoped>

</style>
