<template>
    <v-container>
        <v-card
          class="mx-auto ma-4"
          elevation="10"
          rounded="xl"
          width="600">
            <v-card-title>Solicitar tarjeta</v-card-title>
            <ValidationObserver ref="observer">
                <v-form @submit.prevent="requestCard">
                    <v-container>
                        <v-row dense class="ms-2">
                            <v-col cols="6" class="ps-2">
                                <ValidationProvider
                                v-slot="{ errors }"
                                name="name1"
                                rules="required">
                                    <v-text-field
                                    dense
                                    id="name1"
                                    v-model="form1.name1"
                                    :error-messages="errors"
                                    label="Nombre del solicitante"
                                    color="warning"
                                    filled></v-text-field>
                                </ValidationProvider>
                            </v-col>

                            <v-col cols="6" class="ps-2">
                                <ValidationProvider
                                v-slot="{ errors }"
                                name="name2"
                                rules="required">
                                    <v-text-field
                                    dense
                                    id="name2"
                                    v-model="form1.name2"
                                    :error-messages="errors"
                                    label="A nombre de quien la solicita"
                                    color="warning"
                                    filled></v-text-field>
                                </ValidationProvider>
                            </v-col>
                        </v-row>

                        <v-row
                          x-large
                          dense
                          justify="center"
                          align="center">
                            <v-btn
                              elevation="3"
                              rounded
                              color="warning"
                              type="submit">
                                Solicitar
                            </v-btn>
                        </v-row>
                        <br/>
                    </v-container>
                </v-form>
            </ValidationObserver>
        </v-card>

        <v-card
          class="mx-auto ma-4"
          elevation="10"
          rounded="xl"
          width="600">
            <v-card-title>Recargar tarjeta</v-card-title>
            <ValidationObserver ref="observer2">
                <v-form @submit.prevent="rechargeCard">
                    <v-container>
                        <v-row>
                            <v-col>
                                <ValidationProvider
                                v-slot="{ errors }"
                                name="nameOwer"
                                rules="required">
                                    <v-text-field
                                    id="nameOwer"
                                    v-model="form2.nameOwer"
                                    :error-messages="errors"
                                    label="Nombre del titular"
                                    color="warning"
                                    filled></v-text-field>
                                </ValidationProvider>
                            </v-col>

                            <v-col>
                                <ValidationProvider
                                v-slot="{ errors }"
                                name="cardNumber"
                                rules="required|numeric">
                                    <v-text-field
                                    id="cardNumber"
                                    v-model="form2.cardNumber"
                                    :error-messages="errors"
                                    label="Número de tarjeta"
                                    color="warning"
                                    filled></v-text-field>
                                </ValidationProvider>
                            </v-col>
                        </v-row>

                        <v-row
                          x-large
                          dense
                          justify="center"
                          align="center">
                            <v-btn
                              elevation="3"
                              rounded
                              color="warning"
                              type="submit">
                                Solicitar
                            </v-btn>
                        </v-row>
                        <br/>
                    </v-container>
                </v-form>
            </ValidationObserver>
        </v-card>
  </v-container>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required, numeric } from 'vee-validate/dist/rules'

extend('required', {
  ...required,
  message: 'Este campo es requerido.'
})

extend('numeric', {
  ...numeric,
  message: 'Este campo debe ser numérico.'
})

export default {
  name: 'Form',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      form1: {
        name1: '',
        name2: ''
      },
      form2: {
        nameOwer: '',
        cardNumber: ''
      }
    }
  },
  methods: {
    requestCard () {
      this.$refs.observer.validate()
        .then(result => {
          if (result) {
            alert('La solicitud se ha realizado con éxito')
          }
        })
    },
    rechargeCard () {
      this.$refs.observer2.validate()
        .then(result => {
          if (result) {
            alert('La recarga se ha realizado con éxito')
          }
        })
    }
  }
}
</script>

<style scoped>

</style>
