<template>
  <v-container>
      <v-form>
        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Datos personales</v-card-title>
            <v-row justify="space-around" class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                        id="name"
                        v-model="form.name"
                        label="Nombre"
                        filled
                        ></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                        id="middleName"
                        v-model="form.middleName"
                        label="Segundo nombre"
                        filled></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                        id="lastName"
                        v-model="form.lastName"
                        label="Primer apellido"
                        filled></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="secondLastName"
                      v-model="form.secondLastName"
                      filled
                      label="Segundo apellido"></v-text-field>
                </v-col>
            </v-row>

            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="fatherName"
                      v-model="form.fatherName"
                      filled
                      label="Nombre del padre"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="motherName"
                      v-model="form.motherName"
                      filled
                      label="Nombre de la madre"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="height"
                      v-model="form.height"
                      label="Estatura [cm]"
                      filled
                      type="Number"></v-text-field>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Solicitud de servicio consular</v-card-title>
            <v-row justify="space-around" class="ma-2">
                <v-col md="6" class="pa-2">
                    <v-menu
                      ref="menu"
                      v-model="menu"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto">
                        <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              id="applicationDate"
                              v-model="form.applicationDate"
                              label="Fecha de solicitud"
                              persistent-hint
                              prepend-inner-icon="mdi-calendar"
                              v-bind="attrs"
                              filled
                              @blur="date = parseDate(form.applicationDate)"
                              v-on="on"></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date"
                          no-title
                          @input="menu = false"></v-date-picker>
                    </v-menu>
                </v-col>

                <v-col md="6" class="pa-2">
                    <v-text-field
                      id="passportNumber"
                      v-model="form.passportNumber"
                      filled
                      label="Numero de pasaporte"></v-text-field>
                </v-col>
            </v-row>

            <v-row class="ma-2">
                <v-col md="6" class="pa-2">
                    <v-select
                      id="typeRequest"
                      v-model="form.typeRequest"
                      :items="requests"
                      filled
                      label="Tipo de solicitud"></v-select>
                </v-col>

                <v-col md="6" class="pa-2">
                    <v-select
                      id="typeMigrations"
                      v-model="form.typeMigrations"
                      :items="migrations"
                      filled
                      label="Clasficación migratorial al salir de Cuba"></v-select>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Características personales</v-card-title>
            <v-row justify="space-around" class="ma-2">
                <v-col md="6" class="pa-2">
                    <div>Sexo</div>
                    <v-radio-group
                      id="gender"
                      v-model="form.gender"
                      row>
                        <v-radio
                          label="Masculino"
                          value=1></v-radio>

                        <v-radio
                          label="Feminino"
                          value=2></v-radio>
                    </v-radio-group>

                    <div>Color de ojos</div>
                    <v-radio-group
                      id="eyeColor"
                      v-model="form.eyeColor"
                      row>
                        <v-radio
                          label="Claro"
                          value=1></v-radio>

                        <v-radio
                          label="Negros"
                          value=2></v-radio>

                          <v-radio
                          label="Pardos"
                          value=3></v-radio>
                    </v-radio-group>

                    <div>Color de piel</div>
                    <v-radio-group
                      id="skinColor"
                      v-model="form.skinColor"
                      row>
                        <v-radio
                          label="Blanca"
                          value=1></v-radio>

                        <v-radio
                          label="Negra"
                          value=2></v-radio>

                        <v-radio
                          label="Amarillo"
                          value=3></v-radio>

                        <v-radio
                          label="Meztiza"
                          value=4></v-radio>

                        <v-radio
                          label="Albino"
                          value=5></v-radio>
                    </v-radio-group>

                    <div>Color de pelo</div>
                    <v-radio-group
                      id="hairColor"
                      v-model="form.hairColor"
                      row>
                        <v-radio
                          label="Canoso"
                          value=1></v-radio>

                        <v-radio
                          label="Negro"
                          value=2></v-radio>

                        <v-radio
                          label="Rubio"
                          value=3></v-radio>

                        <v-radio
                          label="Castaño"
                          value=4></v-radio>

                        <v-radio
                          label="Rojo"
                          value=5></v-radio>

                          <v-radio
                          label="Otro"
                          value=6></v-radio>
                    </v-radio-group>
                </v-col>

                <v-col md="6" class="pa-2">
                    <v-textarea
                      v-model="form.specialFeature"
                      label="Características especiales"
                      filled></v-textarea>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Lugar de nacimiento/Fecha de salida</v-card-title>
            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="country"
                      v-model="form.country"
                      prepend-inner-icon="mdi-earth"
                      filled
                      label="País"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="province"
                      v-model="form.province"
                      filled
                      label="Provincia"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="city"
                      v-model="form.city"
                      filled
                      label="Municipio/Ciudad"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-menu
                      ref="menu2"
                      v-model="menu2"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto">
                        <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              id="birthday"
                              v-model="form.birthday"
                              label="Fecha de nacimiento"
                              persistent-hint
                              prepend-inner-icon="mdi-calendar"
                              v-bind="attrs"
                              filled
                              @blur="date2 = parseDate(form.birthday)"
                              v-on="on"></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date2"
                          no-title
                          @input="menu2 = false"></v-date-picker>
                    </v-menu>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-menu
                      ref="menu3"
                      v-model="menu3"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto">
                        <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                              id="departureDate"
                              v-model="form.departureDate"
                              label="Fecha de salida"
                              persistent-hint
                              prepend-inner-icon="mdi-calendar"
                              v-bind="attrs"
                              filled
                              @blur="date3 = parseDate(form.departureDate)"
                              v-on="on"></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date3"
                          no-title
                          @input="menu3 = false"></v-date-picker>
                    </v-menu>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Lugar de residencia</v-card-title>
            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="address"
                      v-model="form.address"
                      filled
                      label="Dirección (Calle, No, %)"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="state"
                      v-model="form.state"
                      filled
                      label="Estado"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="postalCode"
                      v-model="form.postalCode"
                      filled
                      label="Código Postal"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="countryResidence"
                      v-model="form.countryResidence"
                      filled
                      prepend-inner-icon="mdi-earth"
                      label="País de residencia"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="phoneNumber"
                      v-model="form.phoneNumber"
                      filled
                      prepend-inner-icon="mdi-phone"
                      label="Número de teléfono"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="fax"
                      v-model="form.fax"
                      filled
                      label="Fax"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="email"
                      v-model="form.email"
                      filled
                      prepend-inner-icon="mdi-at"
                      label="Email"></v-text-field>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Datos laborales o estudio actual</v-card-title>
            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="work"
                      v-model="form.work"
                      filled
                      label="Centro de Trabajo o Estudio"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="profession"
                      v-model="form.profession"
                      filled
                      label="Profesión"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="occupation"
                      v-model="form.occupation"
                      filled
                      label="Ocupación"></v-text-field>
                </v-col>
            </v-row>

            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workAddress"
                      v-model="form.workAddress"
                      filled
                      label="Dirección"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workProvince"
                      v-model="form.workProvince"
                      filled
                      label="Provincia o Estado"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workCountry"
                      v-model="form.workCountry"
                      filled
                      prepend-inner-icon="mdi-earth"
                      label="Pais"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workZipCode"
                      v-model="form.workZipCode"
                      filled
                      label="Código Postal"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workPhone"
                      v-model="form.workPhone"
                      filled
                      prepend-inner-icon="mdi-phone"
                      label="Teléfono"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workFax"
                      v-model="form.workFax"
                      filled
                      label="Fax"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="workEmail"
                      v-model="form.workEmail"
                      filled
                      prepend-inner-icon="mdi-at"
                      label="Email"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-select
                      id="culturalLevel"
                      v-model="form.culturalLevel"
                      :items="nlv"
                      filled
                      label="Nivel Cultural"></v-select>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Referencia en Cuba</v-card-title>
            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="relationshipName"
                      v-model="form.relationshipName"
                      filled
                      label="Nombre"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="relationshipLastname"
                      v-model="form.relationshipLastname"
                      filled
                      label="Apellido"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="relationship"
                      v-model="form.relationship"
                      filled
                      label="Relación familiar"></v-text-field>
                </v-col>
            </v-row>

            <v-row class="mx-auto ma-4">
                <v-col md="6" class="pa-2">
                    <v-text-field
                      id="relationshipAddress"
                      v-model="form.relationshipAddress"
                      filled
                      label="Dirección"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="relationshipProvince"
                      v-model="form.relationshipProvince"
                      filled
                      label="Provincia"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="relationshipMunicipality"
                      v-model="form.relationshipMunicipality"
                      filled
                      label="Municipio"></v-text-field>
                </v-col>
            </v-row>
        </v-card>

        <v-card class="mx-auto ma-4">
            <v-card-title primary-title class="justify-center">Referencia en Cuba</v-card-title>
            <v-card-subtitle>Las dos últimas direcciones, la más reciente primero.</v-card-subtitle>
            <v-row class="ma-2">
                <v-col md="6" class="pa-2">
                    <v-text-field
                      id="recentAddress1"
                      v-model="form.recentAddress1"
                      filled
                      label="Dirección 1"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="sinceYear1"
                      v-model="form.sinceYear1"
                      filled
                      label="Desde el año"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="untilYear1"
                      v-model="form.untilYear1"
                      filled
                      label="Hasta el año"></v-text-field>
                </v-col>
            </v-row>

            <v-row class="ma-2">
                <v-col md="6" class="pa-2">
                    <v-text-field
                      id="recentAddress2"
                      v-model="form.recentAddress2"
                      filled
                      label="Dirección 2"></v-text-field>
                  </v-col>

                  <v-col md="3" class="pa-2">
                    <v-text-field
                      id="sinceYear2"
                      v-model="form.sinceYear2"
                      filled
                      label="Desde el año"></v-text-field>
                </v-col>

                <v-col md="3" class="pa-2">
                    <v-text-field
                      id="untilYear2"
                      v-model="form.untilYear2"
                      filled
                      label="Hasta el año"></v-text-field>
                </v-col>
            </v-row>

            <v-card-actions>
                <v-btn
                  :loading="loading"
                  plain
                  color="warning"
                  @click="downloadRequest">
                    <v-icon color="warining">mdi-download</v-icon>
                    Descargar solicitud
                </v-btn>
            </v-card-actions>

            <v-card-actions class="justify-center">
                <v-btn
                  color="warning"
                  @click="makeRequest">
                    Solicitar
                </v-btn>
            </v-card-actions>
        </v-card>
      </v-form>
  </v-container>
</template>

<script>
export default {
  name: 'Form',
  data () {
    return {
      menu: false,
      menu2: false,
      menu3: false,
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      date3: new Date().toISOString().substr(0, 10),
      requests: ['Opción 1', 'Opción 2', 'Opción 3', 'Opción 4'],
      migrations: ['Opción 1', 'Opción 2', 'Opción 3', 'Opción 4'],
      countries: ['Canada', 'Cuba', 'Costa Rica'],
      nlv: ['Opción 1', 'Opción 2', 'Opción 3', 'Opción 4'],
      loading: false,
      form: {
        name: '',
        middleName: '',
        lastName: '',
        secondName: '',
        fatherName: '',
        motherName: '',
        height: 0.0,
        applicationDate: this.formatDate(new Date().toISOString().substr(0, 10)),
        passportNumber: '',
        typeRequest: '',
        typeMigrations: '',
        gender: '',
        eyeColor: '',
        skinColor: '',
        hairColor: '',
        specialFeature: '',
        country: '',
        province: '',
        city: '',
        birthday: this.formatDate(new Date().toISOString().substr(0, 10)),
        departureDate: this.formatDate(new Date().toISOString().substr(0, 10)),
        address: '',
        state: '',
        postalCode: '',
        countryResidence: '',
        phoneNumber: '',
        fax: '',
        email: '',
        work: '',
        profession: '',
        occupation: '',
        workAddress: '',
        WorkProvince: '',
        workCountry: '',
        workZipCode: '',
        workPhone: '',
        workFax: '',
        workEmail: '',
        culturalLevel: '',
        relationshipName: '',
        relationshipLastname: '',
        relationship: '',
        relationshipAddress: '',
        relationshipProvince: '',
        relationshipMunicipality: '',
        recentAddress1: '',
        recentAddress2: '',
        sinceYear1: '',
        untilYear1: '',
        sinceYear2: '',
        untilYear2: ''
      }
    }
  },
  computed: {
    computedDateFormatted () {
      return this.formatDate(this.date)
    }
  },
  watch: {
    date (val) {
      this.form.applicationDate = this.formatDate(this.date)
    },
    date2 (val) {
      this.form.birthday = this.formatDate(this.date2)
    },
    date3 (val) {
      this.form.departureDate = this.formatDate(this.date3)
    }
  },
  methods: {
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${month}/${day}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [month, day, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    async downloadRequest () {
      this.loading = true

      await new Promise(resolve => setTimeout(resolve, 3000))

      this.loading = false
    },
    makeRequest () {

    }
  }
}
</script>

<style scoped>

</style>
