<template>
    <v-main>
      <div>
        <v-row class="my-8" align="center" justify="center">
          <v-col cols="7" align="center">
            <h1 style="margin: 15px 0;">ESTADO DE SU CARGA</h1>
            <ValidationObserver ref="observer">
              <form @submit.prevent="searchPackage">
                <v-card outlined style="border-radius: 15px;">
                    <v-card-text>
                      <div class="d-flex flex-row justify-space-around">
                        <v-col cols="12" sm="6">
                          <ValidationProvider v-slot="{ errors }" name="name" rules="required">
                            <v-text-field
                              id="name"
                              v-model="name"
                              :hide-details="errors == '' ? true : false"
                              outlined
                              rounded
                              label="PRIMER NOMBRE"
                              type="text"
                              :error-messages="errors"
                            ></v-text-field>
                          </ValidationProvider>
                        </v-col>
                        <v-col cols="12" sm="6">
                          <ValidationProvider v-slot="{ errors }" name="packageNumber" rules="required">
                            <v-text-field
                              id="packageNumber"
                              v-model="name"
                              :hide-details="errors == '' ? true : false"
                              outlined
                              rounded
                              label="NÚMERO DE PAQUETE"
                              type="text"
                              :error-messages="errors"
                            ></v-text-field>
                          </ValidationProvider>
                        </v-col>
                      </div>
                    </v-card-text>
              </v-card>
              <div class="pt-10 d-flex justify-center">
                  <v-btn plain rounded elevation="2" type="submit">BUCAR</v-btn>
              </div>
              </form>
            </ValidationObserver>
          </v-col>
        </v-row>
      </div>

      <v-divider/>

      <MostUsedServices/>

      <v-divider/>

      <BestSeller/>

      <BestNotices/>
    </v-main>
</template>

<script>
import MostUsedServices from '@/components/MostUsedServices'
import BestSeller from '@/components/BestSeller'
import BestNotices from '@/components/BestNotices'
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('required', {
  validate (value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    }
  },
  message: 'Rellene este campo.',
  computesRequired: true
})

export default {
  name: 'PackageTracking',
  components: {
    MostUsedServices,
    BestSeller,
    BestNotices,
    ValidationObserver,
    ValidationProvider
  },
  data: vm => ({
    name: null,
    packageNumber: null
  }),

  computed: {

  },

  watch: {

  },

  methods: {
    searchPackage () {
      this.$refs.observer.validate()
    }
  }
}
</script>

<style scoped>

</style>
