const mutations = {

  addToCart (state, product) {
    state.cart.push(product)
  },
  removeFromCart (state, product) {
    state.cart = state.cart.filter(item => item.id !== product.id)
  },
  addToFavorites (state, product) {
    if (state.favorites.filter(item => item.id === product.id).length === 0) {
      state.favorites.push(product)
    }
  },
  removeFromFavorites (state, product) {
    state.favorites = state.favorites.filter(item => item.id !== product.id)
  }
}

const actions = {

  updateProductDetails ({ commit }, product) {
    commit('updateProductDetails', product)
    router.replace('/product-details')
  },

  addToCart ({ commit }, product) {
    commit('addToCart', product)
  },

  removeFromCart ({ commit }, product) {
    commit('removeFromCart', product)
  },

  addToFavorites ({ commit }, product) {
    commit('addToFavorites', product)
  },

  removeFromFavorites ({ commit }, product) {
    commit('removeFromFavorites', product)
  }

}

const getters = {
  cart: state => {
    return state.cart
  },
  favorites: state => {
    return state.favorites
  },
  checkProductOnCart: (state) => (id) => {
    return state.cart.filter(product => product.id === id).length
  },
  products: (state) => () => {
    return state.products
  },
  getProduct: (state) => (index) => {
    return state.products[index]
  }
}

export default {
  state,
  mutations,
  actions,
  getters
}
