<template>
  <!-- <v-container class="grey lighten-5"> -->
  <v-container id="services" v-bind:class="{ 'my-auto': showBackground }">
    <div id="background-block" v-if="showBackground"></div>
    <v-row justify="space-between" align="center" style="margin: 0px 145px !important">
      <v-col id="title" cols="5">
        <span>{{ title }}</span>
      </v-col>
      <v-col v-if="showMore" id="all-services-button" class="d-flex flex-column align-center" cols="4">
