      <v-col
        v-for="item in items"
        :key="item.name"
        md="4"
        align="center"
      >
      <v-hover>
          <template v-slot:default="{ hover }">
            <v-card
              class="pa-3 rounded-lg"
              flat
              tile
              :to="item.url"
              height="220"
              max-height="220"
              width="220"
              max-width="220"
              :elevation="hover ? 1 : 0"
            >
                <v-img
                  height="90"
                  width="90"
                  style="margin: 25px 0 !important;"
                  :src="getImgUrl(item.image)"
                  :lazy-src="getImgUrl(item.image)"
                ></v-img>
                <v-card-title class="text--primary d-flex flex-column align-center"> {{item.name}} </v-card-title>
            </v-card>
          </template>
        </v-hover>
      </v-col>
    </v-row>
