<script>
export default {
  name: 'MostUsedServices',
  props: {
    title: {
      type: String,
      default: 'Servicios más usados'
    },
    showMore: {
      type: Boolean,
      default: false
    },
    showBackground: {
      type: Boolean,
      default: true
    }
  },
  data: () => {
    return {
      items: [
        {
          name: 'Recarga Cubacel',
          image: 'Recargas_if.png',
          url: '/services/recharge-cubacel'
        },
        {
