        },
        {
          name: 'Facturas de Alimentos',
          image: 'Facturas_iu.png',
          url: '/services/store'
        }
      ]
    }
  },
  methods: {
    getImgUrl (image) {
      var images = require.context('../assets/home/', false, /\.png$/)
      return images('./' + image)
    }
    // goToDetails (product) {
    //   router.push({ name: 'product-details', params: { product: product } })
    // }
  }
}
