<template>
    <v-container>
        <v-card
          class="mx-auto"
        >

            <ValidationObserver ref="observer">
                <form  @submit.prevent="searchTickets">
                    <v-container>
                        <ValidationProvider
                          v-slot="{ errors }"
                          name="type"
                          rules="required"
                        >
                            <v-radio-group
                              id="type"
                              v-model="ticket.type"
                              row
                              :error-messages="errors">
                                <v-radio
                                label="Ida y vuelta"
                                color="warning"
                                value="0"
                                @change="resetForm"></v-radio>
                                <v-radio
                                label="Una sola vía"
                                color="warning"
                                value="1"
                                @change="resetForm"></v-radio>
                            </v-radio-group>
                        </ValidationProvider>
                        <br>

                        <v-row>
                            <v-col
                            cols="12"
                            sm="6"
                            md="3"
                            >
                                <ValidationProvider
                                  v-slot="{ errors }"
                                  name="field1"
                                  rules="required"
                                >
                                    <v-autocomplete
                                    id='field1'
                                    v-model="ticket.field1"
                                    :items="cities"
                                    dense
                                    label="Origen Ida"
                                    color="warning"
                                    :error-messages="errors"
                                    filled></v-autocomplete>
                                </ValidationProvider>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3"
                            >

                                <ValidationProvider
                                  v-slot="{ errors }"
                                  name="field2"
                                  rules="required"
                                >
                                    <v-autocomplete
                                      id="field2"
                                      v-model="ticket.field2"
                                      :items="cities"
                                      label="Ida Destino"
                                      color="warning"
                                      dense
                                      :error-messages="errors"
                                      filled></v-autocomplete>
                                </ValidationProvider>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3"
                              v-if="ticket.type == 0"
                            >
                                <ValidationProvider
                                  v-slot="{ errors }"
                                  name="field3"
                                  rules="required"
                                >
                                    <v-autocomplete
                                    id="field3"
                                    v-model="ticket.field3"
                                    :items="cities"
                                    label="Regreso Origen"
                                    color="warning"
                                    dense
                                    :error-messages="errors"
                                    filled></v-autocomplete>
                                </ValidationProvider>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3"
                              v-if="ticket.type == 0"
                            >
                                <ValidationProvider
                                  v-slot="{ errors }"
                                  name="field4"
                                  rules="required"
                                >
                                    <v-autocomplete
                                        id="field4"
                                        v-model="ticket.field4"
                                        :items="cities"
                                        dense
                                        color="warning"
                                        label="Regreso destino"
                                        :error-messages="errors"
                                        filled></v-autocomplete>
                                </ValidationProvider>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3">
                                  <v-menu
                                    ref="menu"
                                    v-model="menu"
                                    :close-on-content-click="false"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto"
                                    >
                                    <template v-slot:activator="{ on, attrs }">
                                      <ValidationProvider
                                        v-slot="{ errors }"
                                        name="dateFrom"
                                        rules="required|afterToday">
                                        <v-text-field
                                        id="dateFrom"
                                        v-model="ticket.dateFrom"
                                        label="Desde"
                                        persistent-hint
                                        color="warning"
                                        prepend-inner-icon="mdi-calendar"
                                        v-bind="attrs"
                                        @blur="date = parseDate(ticket.dateFrom)"
                                        v-on="on"
                                        :error-messages="errors"
                                        filled></v-text-field>
                                      </ValidationProvider>
                                    </template>
                                    <v-date-picker
                                        v-model="date"
                                        no-title
                                        color="warning"
                                        @input="menu = false"
                                    ></v-date-picker>
                                    </v-menu>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3"
                              v-if="ticket.type == 0">
                                <v-menu
                                  ref="menu2"
                                  v-model="menu2"
                                  :close-on-content-click="false"
                                  transition="scale-transition"
                                  offset-y
                                  max-width="290px"
                                  min-width="auto">
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="dateUntil"
                                          rules="required|after:@dateFrom">
                                            <v-text-field
                                              id="dateUntil"
                                              v-model="ticket.dateUntil"
                                              label="Hasta"
                                              persistent-hint
                                              color="warning"
                                              prepend-inner-icon="mdi-calendar"
                                              v-bind="attrs"
                                              @blur="date2 = parseDate(ticket.dateUntil)"
                                              v-on="on"
                                              :error-messages="errors"
                                              filled></v-text-field>
                                          </ValidationProvider>
                                    </template>
                                    <v-date-picker
                                        v-model="date2"
                                        no-title
                                        color="warning"
                                        @input="menu2 = false"
                                    ></v-date-picker>
                                </v-menu>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3"
                            >
                                <ValidationProvider
                                  v-slot="{ errors }"
                                  name="adults"
                                  rules="moreThanZero"
                                >
                                    <v-text-field
                                    id="adults"
                                    v-model="ticket.adults"
                                    label="Adultos"
                                    type="number"
                                    color="warning"
                                    :error-messages="errors"
                                    ></v-text-field>
                                </ValidationProvider>
                            </v-col>

                            <v-col
                              cols="12"
                              sm="6"
                              md="3"
                            >
                                <ValidationProvider
                                  v-slot="{ errors }"
                                  name="childs"
                                  rules="positive"
                                >
                                    <v-text-field
                                      id="childs"
                                      v-model="ticket.childs"
                                      label="Infantes"
                                      type="number"
                                      color="warning"
                                      :error-messages="errors"
                                    ></v-text-field>
                                </ValidationProvider>
                            </v-col>
                        </v-row>

                        <v-row>
                          <v-card-actions class="my-2">
                            <v-btn
                            color="warning"
                            dark
                            type="submit"
                            >
                                Buscar
                            </v-btn>
                        </v-card-actions>
                        </v-row>
                    </v-container>
                </form>
            </ValidationObserver>
        </v-card>

        <div>
          <span> Recuerde que si viaja con CUBAMAX puede enviar su dinero al 5%.</span>
        </div>
    </v-container>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'

extend('required', {
  validate (value) {
    return {
      required: true,
      valid: ['', null, undefined].indexOf(value) === -1
    }
  },
  message: 'Rellene este campo.',
  computesRequired: true
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('moreThanZero', {
  validate (value) {
    return value >= 1
  },
  message: 'Escoja un valor válido.'
})

extend('positive', {
  validate (value) {
    return value >= 0
  },
  message: 'Escoja un valor válido.'
})

extend('after', {
  params: ['target'],
  validate (value, { target }) {
    var [day, month, year] = target.split('/')
    const from = new Date(`${month}/${day}/${year}`)

    var [day2, month2, year2] = value.split('/')
    const until = new Date(`${month2}/${day2}/${year2}`)
    return until.getTime() >= from.getTime()
  },
  message: 'Debe elegir una fecha posterior a la ida.'
})

export default {
  name: 'TicketForm',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      cities: ['París', 'Roma', 'La Habana', 'Miami', 'New York'],
      menu: false,
      menu2: false,
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      ticket: {
        type: '0',
        field1: '',
        field2: '',
        field3: '',
        field4: '',
        dateFrom: this.formatDate(new Date().toISOString().substr(0, 10)),
        dateUntil: this.formatDate(new Date().toISOString().substr(0, 10)),
        adults: 0,
        childs: 0
      }
    }
  },
  watch: {
    date (val) {
      this.ticket.dateFrom = this.formatDate(this.date)
    },
    date2 (val) {
      this.ticket.dateUntil = this.formatDate(this.date2)
    }
  },
  methods: {
    searchTickets () {
      this.$refs.observer.validate()
        .then((result) => {
          console.log(result)
          if (result) {
            const tickets = [
              {
                flightNumber: 456,
                schedule: '--'
              },
              {
                flightNumber: 456,
                schedule: '--'
              },
              {
                flightNumber: 456,
                schedule: '--'
              },
              {
                flightNumber: 456,
                schedule: '--'
              },
              {
                flightNumber: 456,
                schedule: '--'
              },
              {
                flightNumber: 456,
                schedule: '--'
              }
            ]

            this.$emit('ticketsSearch', tickets)
          }
        })
    },
    resetForm () {
      this.ticket.field1 = ''
      this.ticket.field3 = ''
      this.ticket.field4 = ''
      this.ticket.dateFrom = new Date().toISOString().substr(0, 10)
      this.ticket.dateUntil = new Date().toISOString().substr(0, 10)
      this.ticket.adults = 0
      this.ticket.childs = 0

      this.$refs.observer.reset()
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

