<template>
  <v-main>

    <Navigation />

    <MenuStore/>

    <div id="product-detail-container"></div>

    <v-row style="margin: 6.5rem 0px 11rem 4rem;">
      <v-col id="img-list" cols="1" align="center">
          <div v-if="product.images.length > 1">
            <v-img v-for="(image, i) in product.images.slice(1)" :key="i+1"
              class="mb-3"
              min-width="100%"
              max-width="100%"
              min-height="80px"
              style="border-radius: 7px;"
              :src="getImgUrl(image)"
              :lazy-src="getImgUrl(image)"
              @click="selectImage(i+1)"
            ></v-img>
          </div>
      </v-col>
      <v-col cols="5">
        <!-- <div class="image-container"> -->
          <v-card min-width="500px" elevation="0" class="rounded-lg"
            max-width="500px"
            min-height="400px">
            <v-img
            min-width="500px"
            max-width="500px"
            min-height="400px"
            style="border-radius: 16px;"
            :src="getImgUrl(selectedImage)"
            :lazy-src="getImgUrl(selectedImage)"
          ></v-img>
            </v-card>
        <!-- </div> -->
      </v-col>
      <v-col cols="5">
        <span id="title">{{ product.title }}</span>
        <!-- <p>{{ product.description }}</p> -->
        <p>
          Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam,
          eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo. Nemo enim ipsam
          voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione
          voluptatem sequi nesciunt.

          Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam.

          Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam.
        </p>
        <v-divider style="position: absolute; width: 37%; margin: 19rem 15px 0 15px !important;"></v-divider>
        <v-row align="center" justify="space-between" class="bottons-row">
          <v-col>
            <v-btn icon @click.prevent="checkOnFavorites(product)">
              <v-icon :color="product.isFavorite? 'red' : 'grey darken-1'">{{ product.isFavorite ? 'mdi-heart' : 'mdi-heart-outline' }}</v-icon>
            </v-btn>
            <v-btn icon @click="addToCart(product)" style="margin-left: 15px">
              <v-badge
                :content="amoutn"
                :value="amoutn"
                color="yellow darken-4"
                overlap
              >
                <v-icon :color="amoutn > 0? 'rgba(255, 102, 0, 1)' : 'grey darken-1'">mdi-cart-plus</v-icon>
              </v-badge>
            </v-btn>
          </v-col>
          <v-col cols="3" align="center">
            <div id="price">${{ product.price }}</div>
          </v-col>
          <v-col cols="3">
            <v-btn id="buy-btn">COMPRAR</v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <ProductsList :slide-mode="true"/>

    <most-used-services />

    <best-notices />
  </v-main>
</template>

<script>
import store from '../store'
import MenuStore from '@/components/MenuStore'
import ProductsList from '@/components/ProductsList'
import MostUsedServices from '@/components/MostUsedServices'
import BestNotices from '@/components/BestNotices'
import Navigation from '@/components/Navigation'

export default {
  name: 'ProductDetails',
  // props: {
  //   product: {
  //     type: Object,
  //     required: true
  //   }
  // },
  data: function () {
    return {
      id: this.$route.params.id,
      product: this.$store.getters.getProduct(this.$route.params.id),
      imageSelected: null
    }
  },
  components: {
    MenuStore,
    MostUsedServices,
    ProductsList,
    BestNotices,
    Navigation
  },
  created: function () {
    this.imageSelected = this.product.images[0]
  },
  watch: {
    '$route' (to, from) {
      this.id = to.params.id
      this.product = this.$store.getters.getProduct(this.id)
      this.imageSelected = this.product.images[0]
    }
  },
  computed: {
    amoutn: {
      get () {
        return store.getters.checkProductOnCart(this.product.id)
      }
    },
    selectedImage: function () {
      console.log(this.product)
      return this.imageSelected
    }
  },
  methods: {
    addToCart (product) {
      this.$store.dispatch('addToCart', product)
    },
    checkOnFavorites (product) {
      if (product.isFavorite) {
        this.$store.dispatch('addToFavorites', product)
      } else {
        this.$store.dispatch('removeFromFavorites', product)
      }
      product.isFavorite = !product.isFavorite
    },
    getImgUrl (image) {
      var images = require.context('../assets/', false)
      return images('./' + image)
    },
    selectImage (imageIndex) {
      const temImg = this.product.images[0]
      this.product.images[imageIndex] = this.product.images[0]
      this.product.images[0] = temImg
      this.imageSelected = this.product.images[0]
    }
  }
}
</script>

<style scoped>
