  background-image: url('../assets/store/background-tienda-detail.png') !important;
  background-size: cover;
}
h1, h2, h3{
   margin: 30px 0;
}
h1, h2, h3, div, p, span{
  cursor: default;
}
#img-list .v-image, .v-badge{
  cursor: pointer !important;
}
.bottons-row {
    margin: 19.5rem 0 0 0;
    position: absolute;
    width: 40%;
}
.image-container {
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  min-width: 400px;
  max-width: 400px;
  min-height: 300px;
  border: 1px solid lightgrey;
}
#product-detail-container{
    width: 1160px;
    height: 520px;
    position: absolute;
    overflow: visible;
    transform: matrix(1,0,0,1,124.9814,90.2515) rotate(-3deg);
    transform-origin: center;
    background: rgba(241,244,246,1);
    /* left: -115px; */
    top: -25px;
    border-radius: 50px;
}
#title{
  position: absolute;
  text-align: left;
  font-family: Raleway-bold;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  color: rgba(0,100,100,1);
  text-transform: uppercase;
  width: 500px;
}
.col p {
    position: absolute;
    margin-top: 80px;
    height: 210px;
    text-align: left;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    color: rgba(112,112,112,1);
    white-space: break-spaces;
    width: 534px;
    overflow-y: auto;
    line-height: normal;
}
.col h2:last-child{
  position: absolute;
  margin-top: 75px;
}
  #price {
    white-space: nowrap;
    text-align: center;
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    color: rgba(48, 153, 153, 1);
  }

  #buy-btn {
    filter: drop-shadow(0px 1px 7px rgba(48, 153, 153, 0.318));
    text-align: center;
    font-family: Raleway;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: rgba(255, 255, 255, 1);
    text-transform: uppercase;
    letter-spacing: normal;
    border-radius: 5px;
    background: rgba(255, 102, 0, 1);
    height: 40px;
    padding: 0px 8px;
}

.v-icon{
  font-size: 28px !important;
}
#services{
  margin: 0px auto 160px !important;
}
</style>
