<template>
  <div>

    <section v-if="slideMode" id="blogs-container">
      <v-row justify="space-between" align="center">
        <v-col id="section-title" align="center" cols="3">
          <span>{{ title }}</span>
        </v-col>
        <v-col id="all-blogs-button" class="d-flex flex-column align-center" cols="4">
          <v-btn elevation="0" @click="showMore('Noticias Cubamax')">VER TODAS LOS ARTÍCULOS</v-btn>
        </v-col>
      </v-row>
      <div class="product-slider-container">
        <v-slide-group
          id="blogs-slider"
          class="pa-2 d-flex flex-row align-center justify-center"
          center-active
          active-class="success"
          show-arrows="always"
        >
          <v-slide-item v-for="(blog, i) in blog" :key="i" v-slot="{ toggle }">
            <Blog :blog="blog" @click="toggle"/>
          </v-slide-item>
        </v-slide-group>
      </div>
    </section>

    <v-row v-if="!slideMode && !listSize" class="px-15">
      <Blog v-for="(blog, i) in blogs" :key="i" :blog="blog" />
    </v-row>

    <v-row v-if="!slideMode && listSize" align="center" style="margin: 0 3rem 1rem">
      <v-col id="section-title" align="left">
        <span>{{ title }}</span>
      </v-col>
    </v-row>
    <v-row v-if="!slideMode && listSize" class="px-15">
      <Blog v-for="(blog, i) in blogs.slice(0, listSize)" :key="i" :blog="blog" />
    </v-row>

    <div v-if="!slideMode && !listSize" class="text-center">
        <v-pagination circle
        v-model="pagination.page"
        :length="Math.ceil(this.blogs.length / 12)"
        :total-visible="15"
        @input="paginate"
        @next="paginate"
        @previous="paginate"
        ></v-pagination>
    </div>

    <v-row v-if="!slideMode && listSize">
      <v-col id="all-blogs-button" class="d-flex flex-column align-center">
        <v-btn @click="showMore(section)" elevation="0" style="margin-left: 0 !important; margin: 55px 0px 12px 0px !important;">Cargar más artículos</v-btn>
      </v-col>
    </v-row>

  </div>
</template>

<script>
import Blog from '@/components/Blog'
import router from '../router'

export default {
  name: 'BlogsList',
  props: {
    slideMode: {
      type: Boolean,
      default: false
    },
    listSize: {
      type: Number,
      required: false
    },
    title: {
      type: String,
      default: 'Artículos más recientes'
    },
    section: {
      type: String,
      required: false
    }
  },
  components: {
    Blog
  },
  data () {
    return {
      pagination: {
        limit: 12,
        offset: 0,
        page: 1,
        filter: null,
        order: '-name',
        total: 0
      },
      blogs: this.$store.getters.blogs()
    }
  },
  created: function () {
    console.log(this.$route)
  },
  methods: {
    getImgUrl (image) {
      var images = require.context('../assets/', false)
      return images('./' + image)
    },
    showMore (section) {
      router.push({ path: this.$route.path + '/' + section.toLowerCase() })
    },
    goToDetails (blog) {
      router.push({ name: 'blog-detail', params: { blog: blog } })
    }
  }
}
</script>

