<template>
  <v-container>
      <v-card class="mx-auto ma-4">
        <ValidationObserver ref="observer">
          <v-form @submit.prevent="submit">
            <v-card-subtitle><strong>Precio:</strong> el trámite de la visa cuesta $75.00</v-card-subtitle>
            <v-card-title class="justify-center">Datos laborales o estudio actual</v-card-title>
            <v-row class="ma-2">
                <v-col md="4" class="pa-2">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="name"
                    rules="required">
                    <v-text-field
                      id="name"
                      label="Nombre"
                      v-model="form.name"
                      color="warning"
                      :error-messages="errors"
                      filled></v-text-field>
                  </ValidationProvider>
                </v-col>

                <v-col md="4" class="pa-2">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="lastname"
                    rules="required">
                    <v-text-field
                      id="lastname"
                      label="Apellido"
                      v-model="form.lastname"
                      color="warning"
                      :error-messages="errors"
                      filled></v-text-field>
                  </ValidationProvider>
                </v-col>

                <v-col md="4" class="pa-2">
                    <v-menu
                      ref="menu"
                      v-model="menu"
                      :close-on-content-click="false"
                      transition="scale-transition"
                      offset-y
                      max-width="290px"
                      min-width="auto">
                        <template v-slot:activator="{ on, attrs }">
                          <ValidationProvider
                            v-slot="{ errors }"
                            name="birthday"
                            rules="required|afterToday">
                              <v-text-field
                                id="birthday"
                                v-model="form.birthday"
                                label="Fecha de recogida"
                                persistent-hint
                                append-icon="mdi-calendar"
                                v-bind="attrs"
                                @blur="date = parseDate(form.birthday)"
                                v-on="on"
                                color="warning"
                                :error-messages="errors"
                                filled></v-text-field>
                          </ValidationProvider>
                        </template>
                        <v-date-picker
                          v-model="date"
                          no-title
                          @input="menu = false"
                        ></v-date-picker>
                    </v-menu>
                </v-col>
            </v-row>

            <v-divider/>

            <v-card-title class="justify-center">Datos del pasaporte</v-card-title>
            <v-row class="ma-2">
                <v-col md="3" class="pa-2">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="passportNumber"
                    rules="required">
                    <v-text-field
                      id="passportNumber"
                      label="Número del pasaporte"
                      :error-messages="errors"
                      v-model="form.passportNumber"
                      filled></v-text-field>
                  </ValidationProvider>
                </v-col>

                <v-col md="3" class="pa-2">
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="passportCountry"
                    rules="required">
                    <v-text-field
                      id="passportCountry"
                      label="País del pasaporte"
                      v-model="form.passportCountry"
                      color="warning"
                      :error-messages="errors"
                      filled></v-text-field>
                  </ValidationProvider>
                </v-col>
            </v-row>

            <v-divider/>

            <v-card-title class="justify-center">Modo entrega</v-card-title>
            <v-row>
                <v-col>
                  <ValidationProvider
                    v-slot="{ errors }"
                    name="deliveryMode"
                    rules="required">
                    <v-radio-group
                      id="deliveryMode"
                      :error-messages="errors"
                      v-model="form.deliveryMode"
                      row>
                        <v-radio
                          label="Recoger en CUBAMAX"
                          color="warning"
                          value="1"></v-radio>

                        <v-radio
                          label="Enviar en USA (USPS)$10"
                          color="warning"
                          value="2"></v-radio>

                        <v-radio
                          label="Enviar en USA (EXPRESS FEDEX)$25"
                          color="warning"
                          value="3"></v-radio>

                        <v-radio
                          label="Entrega en el areopuerto de Miami $15"
                          color="warning"
                          value="4"></v-radio>
                    </v-radio-group>
                  </ValidationProvider>
                </v-col>
            </v-row>

            <v-row
              elevation="3"
              x-large
              justify="center"
              align="center">
                <v-btn
                  rounded
                  color="warning"
                  type="submit">
                    Solicitar</v-btn>
            </v-row>
          </v-form>
        </ValidationObserver>
      </v-card>
  </v-container>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required } from 'vee-validate/dist/rules'

extend('required', {
  ...required,
  message: 'Este campo es requerido.'
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

export default {
  name: 'Form',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      menu: false,
      date: new Date().toISOString().substr(0, 10),
      form: {
        name: '',
        lastname: '',
        birthday: this.formatDate(new Date().toISOString().substr(0, 10)),
        passportNumber: '',
        passportCountry: '',
        deliveryMode: ''
      }
    }
  },
  watch: {
    date (val) {
      this.form.birthday = this.formatDate(this.date)
    }
  },
  methods: {
    submit () {
      this.$refs.observer.validate()
        .then(result => {
          if (result) {
            alert('La solicitud se ha realizado con éxito.')
          }
        })
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

<style scoped>

</style>
