<template>
    <v-container>
        <v-card
          class="mx-auto"
          width="500"
        >
            <ValidationObserver ref="observer">
                <v-form
                  @submit.prevent="findReservations">

                    <v-row class="ma-2">
                        <v-col class="pa-2">
                                <v-menu
                                  ref="menu"
                                  v-model="menu"
                                  :close-on-content-click="false"
                                  transition="scale-transition"
                                  offset-y
                                  max-width="290px"
                                  min-width="auto">
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="checkIn"
                                          rules="required|afterToday">
                                        <v-text-field
                                        id="checkIn"
                                        v-model="reservationForm.checkIn"
                                        label="Salida"
                                        persistent-hint
                                        append-icon="mdi-calendar"
                                        v-bind="attrs"
                                        @blur="date = parseDate(reservationForm.checkIn)"
                                        v-on="on"
                                        color="warning"
                                        :error-messages="errors"
                                        filled></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <v-date-picker
                                        v-model="date"
                                        no-title
                                        @input="menu = false"
                                    ></v-date-picker>
                                    </v-menu>
                        </v-col>

                        <v-col class="pa-2">
                                <v-menu
                                  ref="menu2"
                                  v-model="menu2"
                                  :close-on-content-click="false"
                                  transition="scale-transition"
                                  offset-y
                                  max-width="290px"
                                  min-width="auto">
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="checkOut"
                                          rules="required|after:@checkIn">
                                            <v-text-field
                                            id="deliveryDate"
                                            v-model="reservationForm.checkOut"
                                            label="Regreso"
                                            persistent-hint
                                            color="warning"
                                            append-icon="mdi-calendar"
                                            v-bind="attrs"
                                            @blur="date2 = parseDate(reservationForm.checkOut)"
                                            v-on="on"
                                            :error-messages="errors"
                                            filled></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <v-date-picker
                                        v-model="date2"
                                        no-title
                                        @input="menu2 = false"
                                    ></v-date-picker>
                                    </v-menu>
                        </v-col>
                    </v-row>

                    <v-row class="ma-2">
                        <v-col class="pa-2">
                            <ValidationProvider
                                v-slot="{ errors }"
                                name="destination"
                                rules="required">
                                    <v-text-field
                                    id="pickUpPlace"
                                    v-model="reservationForm.destination"
                                    :error-messages="errors"
                                    label="Seleccione el destino"
                                    prepend-inner-icon="mdi-map-marker"
                                    color="warning"
                                    filled></v-text-field>
                            </ValidationProvider>
                        </v-col>
                    </v-row>

                    <v-row class="ma-2">
                        <v-col class="pa-2">
                            <ValidationProvider
                                v-slot="{ errors }"
                                name="destination"
                                rules="required">
                            <v-select
                                id="rating"
                                :items="items"
                                v-model="reservationForm.rating"
                                label="Clasificación del hotel"
                                :error-messages="errors"
                                prepend-inner-icon="mdi-star"
                                color="warning"
                                filled></v-select>
                            </ValidationProvider>
                        </v-col>
                    </v-row>

                    <v-row
                      elevation="3"
                      x-large
                      justify="center"
                      align="center">
                        <v-btn rounded type="submit" color="warning"> Buscar </v-btn>
                        </v-row>
                        <br>
                </v-form>
            </ValidationObserver>
        </v-card>
  </v-container>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required } from 'vee-validate/dist/rules'

extend('required', {
  ...required,
  message: 'Este campo es requerido.'
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('after', {
  params: ['target'],
  validate (value, { target }) {
    var [day, month, year] = target.split('/')
    const from = new Date(`${month}/${day}/${year}`)

    var [day2, month2, year2] = value.split('/')
    const until = new Date(`${month2}/${day2}/${year2}`)
    return until.getTime() >= from.getTime()
  },
  message: 'Debe elegir una fecha posterior a la ida.'
})

export default {
  name: 'ReservationForm',
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data () {
    return {
      menu: false,
      menu2: false,
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      items: ['1', '2', '3', '4', '5'],
      reservationForm: {
        checkIn: this.formatDate(new Date().toISOString().substr(0, 10)),
        checkOut: this.formatDate(new Date().toISOString().substr(0, 10)),
        destination: '',
        rating: 0
      }
    }
  },
  watch: {
    date (val) {
      this.reservationForm.checkIn = this.formatDate(this.date)
    },
    date2 (val) {
      this.reservationForm.checkOut = this.formatDate(this.date2)
    }
  },
  methods: {
    findReservations () {
      this.$refs.observer.validate()
        .then(result => {
          if (result) {
            const hotels = [
              {
                name: 'El cuartico de Magaly',
                location: 'La chusmita.',
                description: 'Mejor que en tu casa'
              },
              {
                name: 'El cuartico de Magaly',
                location: 'La chusmita.',
                description: 'Mejor que en tu casa'
              },
              {
                name: 'El cuartico de Magaly',
                location: 'La chusmita.',
                description: 'Mejor que en tu casa'
              },
              {
                name: 'El cuartico de Magaly',
                location: 'La chusmita.',
                description: 'Mejor que en tu casa'
              }
            ]

            this.$emit('hotelsFinded', hotels)
          }
        })
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

