<template>
    <v-container>
        <v-card
          class="mx-auto"
          width="700">
            <ValidationObserver ref="observer">
                <v-form
                  @submit.prevent="findCars">
                    <v-container>
                        <v-row>
                            <v-col>
                                <ValidationProvider
                                v-slot="{ errors }"
                                name="pickUpPlace"
                                rules="required">
                                    <v-text-field
                                    id="pickUpPlace"
                                    v-model="carForm.pickUpPlace"
                                    :error-messages="errors"
                                    label="Lugar de recogida"
                                    prepend-inner-icon="mdi-map-marker"
                                    color="warning"
                                    filled></v-text-field>
                                </ValidationProvider>
                            </v-col>
                        </v-row>

                        <v-row justify="space-around">
                            <v-col
                              md="6"
                              class="pa-2">
                                    <v-menu
                                    ref="menu1"
                                    v-model="menu1"
                                    :close-on-content-click="false"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto">
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="pickUpDate"
                                          rules="required|afterToday">
                                        <v-text-field
                                        id="pickUpDate"
                                        v-model="carForm.pickUpDate"
                                        label="Fecha de recogida"
                                        persistent-hint
                                        append-icon="mdi-calendar"
                                        v-bind="attrs"
                                        @blur="date = parseDate(carForm.pickUpDate)"
                                        v-on="on"
                                        color="warning"
                                        :error-messages="errors"
                                        filled></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <v-date-picker
                                        v-model="date"
                                        no-title
                                        @input="menu1 = false"
                                    ></v-date-picker>
                                    </v-menu>
                            </v-col>

                            <v-col
                              md="6"
                              class="pa-2">
                                    <v-menu
                                        ref="menu2"
                                        v-model="menu2"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        :return-value.sync="carForm.pickUpTime"
                                        transition="scale-transition"
                                        offset-y
                                        max-width="290px"
                                        min-width="290px"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="pickUpTime"
                                          rules="required">
                                          <v-text-field
                                              id="pickUpTime"
                                              v-model="carForm.pickUpTime"
                                              label="Hora de entrega"
                                              prepend-inner-icon="mdi-clock-time-four-outline"
                                              readonly
                                              v-bind="attrs"
                                              v-on="on"
                                              color="waning"
                                              :error-messages="errors"
                                              filled></v-text-field>
                                        </ValidationProvider>
                                        </template>
                                        <v-time-picker
                                        v-if="menu2"
                                        v-model="carForm.pickUpTime"
                                        full-width
                                        @click:minute="$refs.menu2.save(carForm.pickUpTime)"
                                        ></v-time-picker>
                                    </v-menu>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col>
                                <ValidationProvider
                                v-slot="{ errors }"
                                name="deliveryPlace"
                                rules="required">
                                    <v-text-field
                                    id="deliveryPlace"
                                    v-model="carForm.deliveryPlace"
                                    :error-messages="errors"
                                    label="Lugar de entrega"
                                    prepend-inner-icon="mdi-map-marker"
                                    color="warning"
                                    filled></v-text-field>
                                </ValidationProvider>
                            </v-col>
                        </v-row>

                        <v-row justify="space-around">
                            <v-col
                              md="6"
                              class="pa-2">
                                    <v-menu
                                    ref="menu3"
                                    v-model="menu3"
                                    :close-on-content-click="false"
                                    transition="scale-transition"
                                    offset-y
                                    max-width="290px"
                                    min-width="auto"
                                    >
                                    <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="deliveryDate"
                                          rules="required|after:@pickUpDate">
                                            <v-text-field
                                            id="deliveryDate"
                                            v-model="carForm.deliveryDate"
                                            label="Fecha de recogida"
                                            persistent-hint
                                            color="warning"
                                            append-icon="mdi-calendar"
                                            v-bind="attrs"
                                            @blur="date2 = parseDate(carForm.deliveryDate)"
                                            v-on="on"
                                            :error-messages="errors"
                                            filled></v-text-field>
                                        </ValidationProvider>
                                    </template>
                                    <v-date-picker
                                        v-model="date2"
                                        no-title
                                        @input="menu3 = false"
                                    ></v-date-picker>
                                    </v-menu>
                            </v-col>

                            <v-col
                              md="6"
                              class="pa-2">
                                    <v-menu
                                        ref="menu4"
                                        v-model="menu4"
                                        :close-on-content-click="false"
                                        :nudge-right="40"
                                        :return-value.sync="carForm.deliveryTime"
                                        transition="scale-transition"
                                        offset-y
                                        max-width="290px"
                                        min-width="290px"
                                    >
                                        <template v-slot:activator="{ on, attrs }">
                                        <ValidationProvider
                                          v-slot="{ errors }"
                                          name="deliveryTime"
                                          rules="required">
                                            <v-text-field
                                              id="deliveryTime"
                                              v-model="carForm.deliveryTime"
                                              label="Hora de recogida"
                                              prepend-inner-icon="mdi-clock-time-four-outline"
                                              readonly
                                              color="warning"
                                              v-bind="attrs"
                                              v-on="on"
                                              :error-messages="errors"
                                              filled></v-text-field>
                                        </ValidationProvider>
                                        </template>
                                        <v-time-picker
                                        v-if="menu4"
                                        v-model="carForm.deliveryTime"
                                        full-width
                                        @click:minute="$refs.menu4.save(carForm.deliveryTime)"
                                        ></v-time-picker>
                                    </v-menu>
                            </v-col>
                        </v-row>

                        <v-row
                          elevation="3"
                          x-large
                          justify="center"
                          align="center">
                            <v-btn
                              rounded
                              color="warning"
                              type="submit">
                                Buscar
                            </v-btn>
                        </v-row>
                        <br/>
                    </v-container>
                </v-form>
            </ValidationObserver>
        </v-card>
    </v-container>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required } from 'vee-validate/dist/rules'

extend('required', {
  ...required,
  message: 'Este campo es requerido.'
})

extend('afterToday', {
  validate (value) {
    const [day, month, year] = value.split('/')
    const date = new Date(`${month}/${day}/${year}`)
    const today = new Date()
    return date.getTime() >= today.getTime()
  },
  message: 'Escoja una fecha posterior a la actual.'
})

extend('after', {
  params: ['target'],
  validate (value, { target }) {
    var [day, month, year] = target.split('/')
    const from = new Date(`${month}/${day}/${year}`)

    var [day2, month2, year2] = value.split('/')
    const until = new Date(`${month2}/${day2}/${year2}`)
    return until.getTime() >= from.getTime()
  },
  message: 'Debe elegir una fecha posterior a la fecha de recogida.'
})

export default {
  name: 'RentcarForm',
  components: {
    ValidationObserver,
    ValidationProvider

  },
  data () {
    return {
      menu1: false,
      menu2: false,
      menu3: false,
      menu4: false,
      date: new Date().toISOString().substr(0, 10),
      date2: new Date().toISOString().substr(0, 10),
      carForm: {
        pickUpPlace: '',
        pickUpDate: this.formatDate(new Date().toISOString().substr(0, 10)),
        pickUpTime: null,
        deliveryPlace: '',
        deliveryDate: this.formatDate(new Date().toISOString().substr(0, 10)),
        deliveryTime: null

      }

    }
  },
  computed: {
    computedDateFormatted () {
      return this.formatDate(this.date)
    }
  },

  watch: {
    date (val) {
      this.carForm.pickUpDate = this.formatDate(this.date)
    },
    date2 (val) {
      this.carForm.deliveryDate = this.formatDate(this.date2)
    }
  },
  methods: {
    findCars () {
      this.$refs.observer.validate()
        .then(result => {
          if (result) {
            const cars = [
              {
                model: 'Audi',
                description: 'The best car.'
              },
              {
                model: 'Audi',
                description: 'The best car.'
              },
              {
                model: 'Audi',
                description: 'The best car.'
              },
              {
                model: 'Audi',
                description: 'The best car.'
              }
            ]

            this.$emit('carsFinded', cars)
          }
        })
    },
    formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (date) {
      if (!date) return null

      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    }
  }
}
</script>

<style scoped>

</style>
