import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/home'
import Cubamax from '@/views/cubamax'
import Ticket from '@/views/ticket'
import Rentcar from '@/views/rentcar'
import Offers from '@/views/offers'
import ProductDetails from '@/components/ProductDetails.vue'
import RechargeCubacel from '../views/recharges/recharge-cubacel.vue'
import RechargeNauta from '../views/recharges/recharge-nauta.vue'
import HotelReservation from '@/views/hotel-reservation'
import MLCCard from '@/views/mlc-card'
import Remittance from '../views/remittance/remittance.vue'
import SendPackage from '../views/send-package/send-package.vue'
import Store from '@/views/store'
import PassportTracking from '../views/tracking/passport-tracking.vue'
import PackageTracking from '../views/tracking/package-tracking.vue'
import ApplyForPassport from '@/views/apply-for-passport'
import VisaApplication from '@/views/visa-application'
import CallToCuba from '@/views/call-to-cuba'
import Contacts from '@/views/contacts'
import Blogs from '../views/blog/blogs.vue'
import Blogs2 from '../views/blog/blogs2.vue'
import BlogDetail from '../views/blog/blog-detail.vue'

Vue.use(VueRouter)

function _prefixRoutes (prefix, routes) {
  return routes.map(route => {
    route.path = `${prefix}/${route.path}`
    return route
  })
}

const routes = [
  {
    path: '/', redirect: 'home'
  },
  {
    path: '/home', name: 'home', component: Home
  },
  {
    path: '/cubamax', name: 'cubamax', component: Cubamax
  },
  {
    path: '/store',
    name: 'store',
    component: Store,
    children: [
      { path: ':section', name: 'store-section', component: Store }
    ]
  },
  {
    path: '/store/:section/:id/details', name: 'product-details', component: ProductDetails
  },
  {
    path: '/offers', name: 'offers', component: Offers
  },
  ..._prefixRoutes('/services', [
    {
      path: 'ticket', component: Ticket
    },
    {
      path: 'rentcar', component: Rentcar
    },
    {
      path: 'hotel-reservation', component: HotelReservation
    },
    {
      path: 'mlc-card', component: MLCCard
    },
    {
      path: 'remittance', component: Remittance
    },
    {
      path: 'recharge-cubacel', component: RechargeCubacel
    },
    {
      path: 'recharge-nauta', component: RechargeNauta
    },
    {
      path: 'send-package', component: SendPackage
    },
    {
      path: 'apply-for-passport', name: 'applyForPassport', component: ApplyForPassport
    },
    {
      path: 'visa-application', name: 'visaapplication', component: VisaApplication
    },
    {
      path: 'call-cuba', name: 'call-to-cuba', component: CallToCuba
    }
  ]),
  {
    path: '/passport-tracking', name: 'passport-tracking', component: PassportTracking
  },
  {
    path: '/package-tracking', name: 'package-tracking', component: PackageTracking
  },
  {
    path: '/contacts', name: 'contacts', component: Contacts
  },
  {
    path: '/blogs2', name: 'blogs2', component: Blogs2
  },
  {
    path: '/blogs',
    name: 'blogs',
    component: Blogs,
    children: [
      { path: ':section', name: 'blogs-section', component: Blogs },
      { path: ':section/recents', name: 'recent-blogs-section', component: Blogs },
      { path: ':section/most-readed', name: 'most-readed-blogs-section', component: Blogs }
    ]
  },
  {
    path: '/blogs/:section/:id', name: 'blog-detail', component: BlogDetail
  }

]

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition
  }
  if (to.hash) {
    return { selector: to.hash }
  }
  if (to.name === 'home') {
    document.getElementById('app-html').style['scroll-behavior'] = 'smooth'
  } else {
    document.getElementById('app-html').style['scroll-behavior'] = ''
  }
  return { x: 0, y: 0 }
}
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior
})

export default router
